import { Link } from "@remix-run/react";

const pages = ["random", "list", "map"];

export default function Index() {
  return (
    <div style={{ fontFamily: "system-ui, sans-serif", lineHeight: "1.4" }}>
      <h1>Where to have lunch?</h1>
      {pages.map((page) => (
        <Link key={page} to={page}>
          {page.toUpperCase()}
        </Link>
      ))}
    </div>
  );
}
